.container1{
    width: 100%;
}

.slick-next {
    display: none!important;
}

.sliderItem {
    background: linear-gradient(45deg, #b9b9b914, #ddf4f724);
    backdrop-filter: blur(2rem);
    border:1px solid #ffffff14;
    margin: auto 2rem;
    border-radius: 2rem;
    padding: 4rem 2rem;    
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    min-height: 80rem;
}

.sliderItemHeading{
    font-size: 2.6rem;
    line-height: 3rem;
    font-weight: bold;
    color: #1a1a1a;
    width: 100%;
}

.sliderItemText{
    font-size: 1.5rem;
    line-height: 2.3rem;
    color: #1a1a1a;
    margin-top: 4rem;
}


.sliderItemText2{
    font-size: 1.5rem;
    line-height: 2.3rem;
    color: #1a1a1a;
}

.pt{
    padding-top: 10rem;
}

@media screen and (max-width: 767px){
    .pt{
        padding-top: 2rem!important;
    }
}

@media (min-width: 870px) and (max-width: 1024px) {
    .sliderItem {
        min-height: 65rem!important;
    }
}

@media screen and (min-width: 1184px){
    .sliderItem {
        min-height: 55rem!important;
    }
}

@media screen and (max-width: 600px){
    .sliderItem {
        min-height: 55rem!important;
    }
}